.dict-container {
  width: calc(100% - 50px);
  height: calc(100% - 65px);
  padding: 25px;
  padding-top: 5px;
}
.dict-word {
  font-size: 30px;
  font-weight: bold;
  line-height: 30px;
  display: inline-block;
  margin-top: 20px;
  margin-left: 5px;
}
.dict-definition {
  line-height: 30px;
}
.dict-original-word {
  opacity: 0.6;
  line-height: 20px;
  margin-top: 10px;
  margin-left: 5px;
}
.dict-word-type {
  font-weight: bold;
  color: #2084e8;
  margin-bottom: 5px;
  margin-top: 5px;
}
.dict-text-box {
  opacity: 1;
  border-radius: 5px;
  margin-top: 5px;
  height: calc(100% - 45px);
  user-select: text;
  overflow-y: scroll;
  padding: 5px;
  box-sizing: border-box;
  font-size: 15px;
  line-height: 20px;
}

.dict-button-container {
  position: absolute;
  bottom: 8px;
  right: 20px;
}
.dict-service-container {
  position: absolute;
  /* width: calc(100% - 32px); */
  top: 30px;
  right: 25px;
}
.dict-service-selector {
  width: 100px;
  height: 30px;
  opacity: 1;
  font-size: 15px;
  line-height: 30px;
  border: none;
  cursor: pointer;
  color: #2084e8;
  background-color: transparent;
}
.add-dialog-shelf-list-option {
  color: black;
}
.dict-url {
  font-size: 12px;
  cursor: pointer;
  text-align: center;
  margin-top: 20px;
}
.dict-container .r0bn4c {
  font-weight: bold;
  color: #2084e8;
}
.dict-container .r0bn4c::before {
  content: "[";
  font-weight: bold;
}
.dict-container .r0bn4c::after {
  content: "]";
  font-weight: bold;
}
.dict-container ol {
  counter-reset: my-counter;
  list-style-type: none;
}

.dict-container li:before {
  counter-increment: my-counter;
  content: counter(my-counter) ". ";
  font-weight: bold;
}
.dict-container li div {
  display: inline;
}
